import { useState } from "react";
import { Bar } from "react-chartjs-2";

import Switch from "@mui/material/Switch";
import {
  FormControl,
  FormControlLabel,
  InputLabel,
  MenuItem,
  Select,
  TextField,
} from "@mui/material";

import {
  Chart as ChartJS,
  Title,
  SubTitle,
  Tooltip,
  Legend,
  ArcElement,
  CategoryScale,
  LinearScale,
  BarElement,
} from "chart.js";

ChartJS.register(
  Title,
  SubTitle,
  Tooltip,
  Legend,
  ArcElement,
  CategoryScale,
  LinearScale,
  BarElement
);

const SampleChart = () => {
  const labels = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
  ];
  const [data, setData] = useState({
    labels,
    datasets: [
      {
        label: "Dataset 1",
        data: [1, 2, 3, 4, 5, 6, 7],
        backgroundColor: "rgba(255, 99, 132, 0.5)",
        borderColor: "rgba(0,0, 0, 0.5)",
        borderWidth: 1,
      },
      {
        label: "Dataset 2",
        data: [7, 6, 5, 4, 3, 2, 1],
        backgroundColor: "rgba(99, 255, 132, 0.5)",
        borderColor: "rgba(0,0, 0, 0.5)",
        borderWidth: 1,
      },
    ],
  });

  const [options, setOptions] = useState({
    plugins: {
      legend: {
        position: "bottom",
        title: { display: true, text: "Legend Title" },
        align: "center",
      },
      title: { display: true, text: "Title", font: { size: 13 } },
      subtitle: { display: true, text: "Subtitle", font: { size: 13 } },
    },
  });
  const toggleTitleDisplay = () => {
    options.plugins.title.display = !options.plugins.title.display;
    const updatedOptions = { ...options };
    setOptions(updatedOptions);
  };

  const changeTitleText = (e) => {
    options.plugins.title.text = e.target.value;
    if (e.target.value === "") {
      options.plugins.title.text = "Title";
    }
    const updatedOptions = { ...options };
    setOptions(updatedOptions);
  };

  const changeTitleFontSize = (e) => {
    options.plugins.title.font.size = e.target.value;
    if (e.target.value === "" || e.target.value <= 0 || e.target.value > 100) {
      options.plugins.title.font.size = 13;
    }
    const updatedOptions = { ...options };
    setOptions(updatedOptions);
  };

  const toggleSubTitleDisplay = () => {
    options.plugins.subtitle.display = !options.plugins.subtitle.display;
    const updatedOptions = { ...options };
    setOptions(updatedOptions);
  };
  const changeSubtitleText = (e) => {
    options.plugins.subtitle.text = e.target.value;
    if (e.target.value === "") {
      options.plugins.subtitle.text = "Subtitle";
    }
    const updatedOptions = { ...options };
    setOptions(updatedOptions);
  };
  const changeSubtitleFontSize = (e) => {
    options.plugins.subtitle.font.size = e.target.value;
    if (e.target.value === "" || e.target.value <= 0 || e.target.value > 100) {
      options.plugins.subtitle.font.size = 13;
    }
    const updatedOptions = { ...options };
    setOptions(updatedOptions);
  };
  const changeLegendPosition = (e) => {
    options.plugins.legend.position = e.target.value;

    const updatedOptions = { ...options };
    setOptions(updatedOptions);
  };
  const changeLegendAlignment = (e) => {
    options.plugins.legend.align = e.target.value;

    const updatedOptions = { ...options };
    setOptions(updatedOptions);
  };
  return (
    <>
      <div style={{ width: "50%", margin: "auto", marginTop: "20px" }}>
        <Bar data={data} options={options} />
      </div>
      <div className=" d-flex container row m-auto justify-content-around">
        <div className="d-flex flex-column col-3 m-3">
          <h2>Title</h2>
          <FormControlLabel
            control={<Switch />}
            label="Display Title"
            checked={options.plugins.title.display}
            onChange={toggleTitleDisplay}
          />
          <TextField
            label="Title Text"
            size="small"
            margin="normal"
            onChange={(e) => changeTitleText(e)}
          />
          <TextField
            label="Title Text Font Size"
            size="small"
            margin="normal"
            onChange={(e) => changeTitleFontSize(e)}
            type="number"
            inputProps={{ min: 0, max: 100 }}
          />
        </div>
        <div className="d-flex flex-column col-3 m-3">
          <h2>Subtitle</h2>
          <FormControlLabel
            control={<Switch />}
            label="Display Subtitle"
            checked={options.plugins.subtitle.display}
            onChange={toggleSubTitleDisplay}
          />

          <TextField
            label="Subtitle Text"
            size="small"
            margin="normal"
            onChange={(e) => changeSubtitleText(e)}
          />

          <TextField
            label="Subtitle Text Font Size"
            size="small"
            margin="normal"
            onChange={(e) => changeSubtitleFontSize(e)}
            type="number"
            inputProps={{ min: 0, max: 100 }}
          />
        </div>

        <div className="d-flex flex-column col-3 m-3">
          <h2>Legend</h2>
          <FormControl className="my-1">
            <InputLabel id="legend-position-label">Position</InputLabel>
            <Select
              labelId="legend-position-label"
              id="legend-position"
              label="position"
              value={options.plugins.legend.position}
              onChange={changeLegendPosition}
              size="small"
            >
              <MenuItem value={"top"}>Top</MenuItem>
              <MenuItem value={"right"}>Right</MenuItem>
              <MenuItem value={"bottom"}>Bottom</MenuItem>
              <MenuItem value={"left"}>Left</MenuItem>
            </Select>
          </FormControl>
          <FormControl className="my-2">
            <InputLabel id="legend-position-label">Alignment</InputLabel>
            <Select
              labelId="legend-alignment-label"
              id="legend-alignment"
              label="alignment"
              value={options.plugins.legend.align}
              onChange={changeLegendAlignment}
              size="small"
            >
              <MenuItem value={"start"}>Start</MenuItem>
              <MenuItem value={"center"}>Center</MenuItem>
              <MenuItem value={"end"}>End</MenuItem>
            </Select>
          </FormControl>
        </div>
      </div>
    </>
  );
};

export default SampleChart;
