import axios from "axios";
import { useState, useEffect } from "react";

import { Bar } from "react-chartjs-2";
import { Doughnut } from "react-chartjs-2";
import { CircularProgress } from "@mui/material";

import {
  Chart as ChartJS,
  Title,
  SubTitle,
  Tooltip,
  Legend,
  ArcElement,
  CategoryScale,
  LinearScale,
  BarElement,
} from "chart.js";
ChartJS.register(
  Title,
  SubTitle,
  Tooltip,
  Legend,
  ArcElement,
  CategoryScale,
  LinearScale,
  BarElement
);

const DataCharts = () => {
  const [loading, setLoading] = useState(false);
  const uri = "https://react-mongo-lloyd.herokuapp.com/api/";
  const barPlaceholder = {
    labels: ["Placeholder"],
    datasets: [
      {
        label: "Placeholder",
        backgroundColor: "",
        borderColor: "",
        borderWidth: 1,
        data: [],
      },
    ],
  };
  const doughnutPlaceholder = {
    labels: [],
    datasets: [
      {
        label: "",
        data: [],
        backgroundColor: [""],
        borderColor: [""],
        borderWidth: 1,
      },
    ],
  };
  const [barChartData, setBarChartData] = useState(barPlaceholder);
  const [salesChartData, setSalesChartData] = useState(barPlaceholder);
  const [doughnutData, setDoughnutData] = useState(doughnutPlaceholder);
  const [salesDoughnutData, setSalesDoughnutData] =
    useState(doughnutPlaceholder);

  useEffect(() => {
    setLoading(true);
    const getTransactions = async () => {
      const fruitsTotalSales = await axios.get(
        `${uri}transactions/view/totalFruitSaleCount`
      );
      const fruitsCount = await axios.get(
        `${uri}transactions/view/transactionFruitCount`
      );

      getItems(fruitsCount.data.data, fruitsTotalSales.data.data);
      setLoading(false);
    };
    const getItems = (fruitsCount, totalSales) => {
      const labels = [];

      const salesData = [];
      const countData = [];

      totalSales.forEach((fruit) => labels.push(fruit._id));
      totalSales.forEach((number) => salesData.push(number.totalSales));
      fruitsCount.forEach((fruit) => countData.push(fruit.count));

      setBarChartData({
        labels,
        datasets: [
          {
            label: "Total Sales",
            backgroundColor: "rgba(75,192,192,1)",
            borderColor: "rgba(0,0,0,1)",
            borderWidth: 1,
            data: salesData,
          },
          {
            label: "Times bought",
            backgroundColor: "rgba(175,132,192,1)",
            borderColor: "rgba(0,0,0,1)",
            borderWidth: 1,
            data: countData,
          },
        ],
      });
      setSalesChartData({
        labels,
        datasets: [
          {
            label: "Times bought",
            backgroundColor: "rgba(175,132,192,1)",
            borderColor: "rgba(0,0,0,1)",
            borderWidth: 1,
            data: countData,
          },
        ],
      });
      setDoughnutData({
        labels,
        datasets: [
          {
            label: "Total# of Sales",
            data: salesData,
            backgroundColor: [
              "rgba(255, 99, 132, 0.8)",
              "rgba(54, 162, 235, 0.8)",
              "rgba(255, 206, 86, 0.8)",
              "rgba(75, 192, 192, 0.8)",
              "rgba(153, 102, 255, 0.8)",
            ],
            borderColor: [
              "rgba(255, 255, 255, 1)",
              "rgba(255, 255, 255, 1)",
              "rgba(255, 255, 255, 1)",
              "rgba(255, 255, 255, 1)",
              "rgba(255, 255, 255, 1)",
            ],
            borderWidth: 1,
          },
        ],
      });

      setSalesDoughnutData({
        labels,
        datasets: [
          {
            label: "Times Bought",
            data: countData,
            backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(255, 206, 86, 0.2)",
              "rgba(75, 192, 192, 0.2)",
              "rgba(153, 102, 255, 0.2)",
            ],
            borderColor: [
              "rgba(255, 70, 10, 1)",
              "rgba(54, 162, 235, 1)",
              "rgba(255, 206, 86, 1)",
              "rgba(75, 192, 192, 1)",
              "rgba(153, 102, 255, 1)",
            ],
            borderWidth: 1,
          },
        ],
      });
    };

    getTransactions();
  }, []);

  return (
    <>
      {loading ? (
        <div
          style={{
            width: "100%",
            height: "93vh",
            textAlign: "center",
            margin: "50%,50%",
          }}
        >
          <CircularProgress />
        </div>
      ) : (
        <div className="container">
          <div className="row">
            <div className="col-12 col-lg-6">
              <Bar
                data={barChartData}
                options={{
                  plugins: {
                    legend: { position: "bottom" },
                    title: {
                      display: true,
                      text: "Sales Report",
                      font: { size: 15 },
                    },
                  },
                }}
              />
            </div>
            <div className="col-12 col-lg-6">
              <Bar
                data={salesChartData}
                options={{
                  plugins: {
                    legend: { position: "bottom" },
                    title: {
                      display: true,
                      text: "Sample Title",
                      font: { size: 15 },
                    },
                    subtitle: {
                      display: true,
                      text: "Sub Title",
                      padding: { bottom: 10 },
                    },
                  },
                }}
              />
            </div>
          </div>
          <div className="row d-flex justify-content-around">
            <div className="col-6 col-lg-3">
              <Doughnut
                data={doughnutData}
                options={{
                  plugins: {
                    legend: { position: "right" },
                    title: {
                      display: true,
                      text: "Total Sales",
                      font: { size: 15 },
                    },
                  },
                }}
              />
            </div>
            <div className="col-6 col-lg-3">
              <Doughnut
                data={salesDoughnutData}
                options={{
                  plugins: {
                    legend: { position: "right", align: "center" },
                    title: {
                      display: true,
                      text: "Sales",
                      font: { size: 15, family: "Helvetica" },
                    },
                    subtitle: { display: true, text: "by # of times bought" },
                  },
                }}
              />
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default DataCharts;
